






























































































































import {Component, Vue} from 'vue-property-decorator';

@Component({
    components: {},
})
export default class Home extends Vue {
    bloggerInfo: any = {};

    created() {
    }

    mounted() {
        (async () => {
            let bloggerInfo = await this.$githubApi.getBloggerInfo();
            this.bloggerInfo = bloggerInfo;
        })()
    }

}
